import pie from './pie.run.js'
import line from './line.run.js'

export default class Page {

  static init (opt) {
    opt.parts.forEach((p) => {
      switch (p.type.toLowerCase()) {
        case 'partchartpie':
          pie.run(p)
          break
        case 'partchartline':
          line.run(p)
          break

      }
    })
  }
}





